.slider {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.slider__images {
  position: relative;
  display: block;
  width: 320px;
  height: 240px;

  @media (min-width: @tablet-width) {
    width: 690px;
    height: 517px;
  }

  @media (min-width: @desktop-width) {
    overflow: hidden;
  }
}

/*Картинки*/
.slider__img {
  display: block;
  width: 320px;
  height: 240px;

  @media (min-width: @tablet-width) {
    position: relative;
    top: 5px;
    left: 9px;
    width: 690px;
    height: 517px;
  }

  @media (min-width: @desktop-width) {
    position: relative;
    top: 0;
    left: 78px;
  }
}

.slider__img--hide {
  display: none;
}

/*Кнопки*/
.slider__buttons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-self: stretch;
  padding: 26px 39px 0 43px;

  @media (min-width: @tablet-width) {
    padding: 14px 69px;
    padding-top: 20px;
  }

  @media (min-width: @desktop-width) {
    padding: 35px 0 38px 198px;
  }
}

.slider__button {
  .indent-reset;
  padding: 5px 0;
  border: none;
  border-bottom: @grey-border-8;
  background-color: @basic-grey-2;
  .oswald-regular;
  text-transform: uppercase;
  .set-font-size-line-height(16px; 20px);
  cursor: pointer;
  transition: 0.3s;

  @media (min-width: @tablet-width) {
    background-color: @basic-grey-2;
    .set-font-size-line-height(20px; 30px);
    border-bottom: 1px dotted @basic-black;
    .indent-reset;
  }

  &:hover {
    border-bottom: 1px solid @basic-black;
  }

  &:active {
    border: none;
  }
}

/*Переключатель в мобилке*/
.slider__toggle {
  position: relative;
  width: 84px;
  height: 14px;
  background-color: @basic-white;
  border: @grey-border-2;
  border-radius: 6px;
  padding: 3px 5px;

  @media (min-width: @tablet-width) {
    display: none;
  }
}

.slider__toggle span {
  position: absolute;
  left: 5px;
  display: block;
  background-color: @basic-green;
  border-radius: 3px;
  width: 35px;
  height: 6px;
}

/*Переключатель в планшете*/
.slider__scale {
  display: none;

  @media (min-width: @tablet-width) {
    position: relative;
    display: block;
    width: 428px;
    height: 6px;
    border-radius: 6px;
    background-color: @special-grey-5;
    margin-left: 23px;
    margin-right: 21px;
  }
}

.slider__scale .slider__scale-bar {
  position: absolute;
  left: 199px;
  top: -14px;
  width: 34px;
  height: 34px;
  background-color: @basic-white;
  border-radius: 50%;
  border: @grey-border-2;
  box-shadow: 0 1px 3px @opacity-grey-3;
  cursor: pointer;
  transition: 0.1s;

  &:hover {
    box-shadow: 0 2px 4px @opacity-grey-4;
  }

  &:active {
    border: @green-border-2;
  }
}

.slider__scale .slider__scale-bar::before {
  content: "";
  position: absolute;
  top: 12px;
  left: 12px;
  width: 8px;
  height: 8px;
  background-color: @basic-green;
  border-radius: 50%;
}
